<template>
  <div id="app" class="flyout">
    <!-- navbar -->
    <navbar position="top" class="navbar navbar-expand-lg cyan accent-4 fixed-top"  src="./static/gl_logo.png" href="/" scrolling>
      <navbar-collapse class="ml-4"> 
        <navbar-nav> 
          <navbar-item class="g-nav-item" href="#" waves-fixed>Home</navbar-item> 
          <navbar-item class="g-nav-item" href="#" waves-fixed>About</navbar-item> 
          <navbar-item class="g-nav-item" href="#" waves-fixed>Features</navbar-item>
          <navbar-item class="g-nav-item" href="#" waves-fixed>Services</navbar-item> 
          <navbar-item class="g-nav-item" href="#" waves-fixed>Team</navbar-item> 
          <navbar-item class="g-nav-item" href="#" waves-fixed>Contact</navbar-item>  
        </navbar-nav> 
        <ul class="navbar-nav nav-flex-icons">
          <li class="nav-item nav-item--social">
            <a href="nav-link">
              <i class="fa fa-facebook title nav-item__icon" aria-hidden="true"></i>
            </a>
          </li>
          <li class="nav-item nav-item--social">
            <a href="nav-link">
              <i class="fa fa-instagram title nav-item__icon" aria-hidden="true"></i>
            </a>
          </li>
          <li class="nav-item nav-item--social">
            <a href="nav-link">
              <i class="fa fa-google-plus title nav-item__icon" aria-hidden="true"></i>
            </a>
          </li>
        </ul>
      </navbar-collapse> 
    </navbar>
    <!-- navbar end -->
    <!-- render all pages here -->
    <main :style="{marginTop: '72px'}">
      <router-view></router-view>
    </main>
    <!-- footer -->
    <Ftr color="cyan accent-4">
      <g-footer></g-footer>
    </Ftr>
    <!-- footer end -->
  </div>
</template>

<script>
import Navbar from '@/components/Navbar.vue';
import NavbarItem from '@/components/NavbarItem.vue';
import NavbarNav from '@/components/NavbarNav.vue';
import NavbarCollapse from '@/components/NavbarCollapse.vue';
import Ftr from '@/components/Footer.vue';
import EdgHd from '@/components/EdgeHeader.vue';
import Container from '@/components/Container.vue';

import GFooter from '@/docs/Footer.vue';
// icons
// import Mdinput from '@/components/MdInput.vue';
// import drop from '@/mixins/drop';

export default {
  name: 'app',
  components: {
    Navbar,
    NavbarItem,
    NavbarNav,
    NavbarCollapse,
    Ftr,
    EdgHd,
    Container,
    // Mdinput
    // drop,
    GFooter
  },
  created (){
    new WOW().init();
  }
};

</script>

<style lang="scss">
.flyout {
	display:flex;
	flex-direction: column;
	min-height:100vh;
	justify-content: space-between;
}

.g-btn-rounded{
  border-radius: 25px !important;
}

.g-btn-text-white {
  color: #fff !important;
}

.g-blue-gradient {
  background: linear-gradient(40deg,#00b8d4,#18ffff);
  &:active{
    background: linear-gradient(40deg,#00b8d4,#18ffff) !important;
  }
}

.navbar-brand img{
  max-width: 40px;
}

.btn-main-blue{
  color: #00b8d4 !important;
  background: #fff;
}

.g-nav-item{
  &:hover{
    background-color: rgba(255, 255, 255, 0.1);
  }
}

.navbar-nav .nav-item--social a {
  padding: 0 0.6rem;
  color: #fff;
  font-size: 18px;
}

.nav-item__icon{
  &:hover{
        transform: translateY(-2px);
    // transform: scale(1.05);
    // color: #f95169;
  }
}

</style>



